:root{
  --colorFont: rgb(150, 185, 216);
  --colorfont2: rgb(0, 0, 0);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
}

html {
  min-height: 100%;
}

body {
  min-height: 100%;
  background: linear-gradient(to right, #0f2027, #203a43, #2c5364); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  font: 1.4em/1.8 'Open Sans', sans-serif;
}

.title{
  color: var(--colorFont);
  text-align: center;
  margin-bottom: 3rem;
}

.sub_title{
  color: var(--colorFont);
  text-align: center;
}

.container{
  display: flex;
  flex-direction: column;
}

#formulario{
  display: flex;
  flex-direction: column;
  align-items: center;
}

select{
  margin-top: 10px;
  margin-bottom: 40px;
  width: 50vw;
  height: 30px;
}

button{
  border-radius: 5px;
  margin-top: 35px;
  display: inline-block;
  align-self: center;
  font-size: 1em;
  height: 35px;
  width: 40vw;
  background-color: aquamarine;
  border: none;
  cursor: pointer;
}

h4{
  font-size: 1.3rem;
}

.container2{
  width: 64%;
  margin: 0 auto;
  color: var(--colorFont);
  display: flex;
  justify-content: center;
  padding: 10rem 0;
}

.card{
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 84%;
  border-radius: 8px;
  background-color: #15273f;
}

@media screen and (max-width:811px) {
  .container2{
    padding: 4rem 0;
    width: 92%;
  }

  .card{
    width: 100%;
    padding: 1rem;
  }
}
